@import "https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700;900&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

body {
  background-image: url("../Section-Background.5567bf85.png");
  background-size: cover;
  position: relative;
}

.contact-navbar {
  z-index: 10;
  justify-content: space-between;
  align-items: center;
  padding: 5em 10em;
  transition: -webkit-backdrop-filter .5s, backdrop-filter .5s, background-color .5s;
  display: flex;
  top: 0;
  left: 0;
  right: 0;
}

.navbar-background {
  -webkit-backdrop-filter: blur(2rem);
  backdrop-filter: blur(2rem);
  background-color: #030716d0;
}

.contact-navbar img {
  height: 16px;
}

.contact-navbar ul {
  gap: 5em;
  display: flex;
}

.contact-navbar ul li {
  list-style: none;
}

.contact-navbar ul li a {
  color: #eee;
  text-decoration: none;
  transition: all .2s;
}

.current-page {
  color: #0000;
  background-image: linear-gradient(to right, #409dea, #7fe3f9);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-background-clip: text;
  -moz-background-clip: text;
  -webkit-text-fill-color: transparent;
  -moz-text-fill-color: transparent;
  font-weight: 700;
}

.contact-navbar ul li a:hover {
  color: #0000;
  background-image: linear-gradient(to right, #409dea, #7fe3f9);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-background-clip: text;
  -moz-background-clip: text;
  -webkit-text-fill-color: transparent;
  -moz-text-fill-color: transparent;
}

footer {
  background-color: #0a0e1e;
  justify-content: center;
  align-items: center;
  padding: 3em 0;
  display: flex;
}

footer p {
  color: #aeb7be;
  font-size: .8em;
}

